<template>
  <div>
       <homeheader></homeheader>
       <img src="../../assets/image/specialbg.jpg" height="200px" width="100%">
    <div class="page-loadmore-wrapper pt_fontSizeTwo" ref="wrapper" :style="{ height: wrapperHeight + 'px' }"> 
      <mt-loadmore :top-method="loadTop" @translate-change="translateChange" @top-status-change="handleTopChange" @bottom-status-change="handleBottomChange"       :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">
        
        <ul class="page-loadmore-list pt_fontSizeTwo">
          <li v-for="item in list" class="page-loadmore-listitem">{{ item }}</li>
        </ul>
       <div slot="top" class="mint-loadmore-top pt_fontSizeTwo">
        <span v-show="topStatus !== 'loading'" :class="{ 'is-rotate': topStatus === 'drop' }">↓</span>
          <span v-show="topStatus === 'loading'">刷新中
          </span>
        </div>    
        <div slot="bottom" class="mint-loadmore-bottom pt_fontSizeTwo">
          <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">↑</span>
          <span v-show="bottomStatus === 'loading'">加载中
          </span>
        </div>
      </mt-loadmore>
    </div>
  </div>
</div>
</template>
<script type="text/babel">
import homeheader from '../common/homeheader'
  export default {
    data() {
      return {
        list: [1,2,3,4,5,1,2,3,4,51],
        allLoaded: false,
        bottomStatus: '',
        wrapperHeight: 0,
        topStatus: '',
        translate: 0,
        moveTranslate: 0,
      };
    },
    components:{
        'homeheader':homeheader
    },
    methods: {
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      loadBottom() {
        //  setTimeout(() => {
        //   let firstValue = this.list[this.list.length-1];
        //   for (let i = 1; i <= 10; i++) {
        //     this.list.push(firstValue + i);
        //   }
        //   this.$refs.loadmore.onBottomLoaded();
        // }, 1500);
         //  setTimeout(() => {
         //     for(var i=0;i<10;i++){
         //      this.list.push(i);
         // }
         //       this.$refs.loadmore.onBottomLoaded();
         //  }, 1500);
        setTimeout(() => {
          // let lastValue = this.list[this.list.length - 1];
          // if (lastValue < 40) {
          //   for (let i = 1; i <= 10; i++) {
          //     this.list.push(lastValue + i);
          //   }
          // } else {
          //   this.allLoaded = true;
          // }
          this.$refs.loadmore.onBottomLoaded();
        }, 1500);
      },
      handleTopChange(status) {
        this.moveTranslate = 1;
        this.topStatus = status;
      },
      handleBottomChange(status){
         this.moveTranslate = 1; 
         this.bottomStatus = status;
      },
      translateChange(translate) {
        const translateNum = +translate;
        this.translate = translateNum.toFixed(2);
        this.moveTranslate = (1 + translateNum / 70).toFixed(2);
      },
      loadTop() {
        setTimeout(() => {
          // let firstValue = this.list[0];
          // for (let i = 1; i <= 10; i++) {
          //   this.list.unshift(firstValue - i);
          // }
        // this.$router.go(0)
          this.$refs.loadmore.onTopLoaded();
        }, 1500);
        console.log(this.topStatus)
      },
},
    // created() {
    //   for (let i = 1; i <= 20; i++) {
    //     this.list.push(i);
    //   }
    // },

    mounted() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
    }
  };
</script>
<style>
.loading-background, .mint-loadmore-top span {
    -webkit-transition: .2s linear;
    transition: .2s linear
}
.mint-loadmore-top span {
    display: inline-block;
    vertical-align: middle
}

.mint-loadmore-top span.is-rotate {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg)
}

.page-loadmore .mint-spinner {
    display: inline-block;
    vertical-align: middle
}

.page-loadmore-desc {
    text-align: center;
    color: #666;
    padding-bottom: 5px
}

.page-loadmore-desc:last-of-type,
.page-loadmore-listitem {
    border-bottom: 1px solid #eee
}

.page-loadmore-listitem {
    height: 50px;
    line-height: 50px;
    text-align: center
}

.page-loadmore-listitem:first-child {
    border-top: 1px solid #eee
}

.page-loadmore-wrapper {
    overflow: scroll
}

.mint-loadmore-bottom span {
    display: inline-block;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    vertical-align: middle
}

.mint-loadmore-bottom span.is-rotate {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg)
}

</style>


